body {
  margin: 0px;
}
/* color legend styles for charts */
.legend-container{
  display: flex;
  flex-wrap: wrap;
  height: 12px;
  padding: 0px;
}
.legend-square{
  width: 12px;
  height: 12px;
  margin-top: 5px;
}
.legend-label{
  font-size: .9rem;
  padding: 0px 16px 0 8px;
}
/* other */
.vis-div{
  background-color: var(--gray-200);
  padding: 24px 32px;
}
.vis-div h6{
  color: var(--gray-700);
}
.column-flex{
  display: flex;
  flex-direction: column;
  flex: 1 1 22.5rem;
  justify-content: space-between;
  height: 340px;
}
.stat-card-source{
  color: var(--gray-500);
  font-size: 1rem;
  line-height: 1.4rem;
}
.stat-card-notes{
  color: var(--gray-600);
  font-size: .9rem;
}
.stat-card-description{
  font-size: 1.25rem;
}
.flex-inner-div-0{
  flex: 1 1 10rem;
}
.flex-inner-div-1{
  flex: 3 1 45rem;
}
.flex-inner-div-1a{
  flex: 1 1 15rem;
}
.hide-div{
  display: none;
}
/* map and legend styles */
.map{
  height: 400px;
  width: 100%;
  border-bottom: #f7f7f7 1px solid;
}
.map-legend-container{
  padding: 5px 10px;
  background-color: #FFF;
}
.map-legend-container .title{
  padding-bottom: 5px;
  font-size: 0.9rem;
}
.rwi-legend{
  padding-bottom: 0.9rem;
}
.rwi-legend-pattern{
  background-image: url('../assets/diagonal-hatch-white_30.png');
  width: 300px;
  height: 12px;
  background-color:#ccc;
}
.undp-accordion.no-background.ant-collapse-small .ant-collapse-item {
  border-top: 1px solid var(--gray-500) !important;
}
.undp-accordion.no-background.ant-collapse-small .ant-collapse-item .ant-collapse-header-text {
  font-size: 14px !important;
  text-transform: none !important;
}
.undp-accordion.no-background.ant-collapse-small .ant-collapse-content-box {
  padding-bottom: 0px !important
}
.ant-segmented-item-selected {
  font-weight: bold;
}
.ant-tooltip-placement-bottom {
    padding-top: 0 !important;
}
.ant-tooltip-placement-top {
    padding-bottom: 0 !important;
}
.underline {
  text-decoration: underline;
}
.table-cell-no-border {
  border-bottom: none !important;
}
.undp-country-header {
  position: relative !important;
}

.undp-segmented-small.data-platform-segmented.ant-segmented{
  background: transparent;
}

.undp-segmented-small.data-platform-segmented .ant-segmented-item-selected{
  color: #000;
  box-shadow: none;
  background: #90FFFF;
}
.undp-segmented-small.data-platform-segmented .ant-segmented-thumb{
  color: #000;
  box-shadow: none;
  border-radius: 0;
  background: #90FFFF;
}

.undp-segmented-small.data-platform-segmented.ant-segmented .ant-segmented-item {
  border-radius: 0;
  position: relative;
}
.undp-segmented-small.data-platform-segmented.ant-segmented .ant-segmented-item:after {
  content: '';
  position: absolute;
  left: -1px;
  top: 6px;
  bottom: 6px;
  width: 1px;
  background: #cccccc;
  height: auto;
}
.undp-segmented-small.data-platform-segmented.ant-segmented .ant-segmented-item:first-child:after {
  content: none;
}

.undp-segmented-small.data-platform-segmented-small .ant-segmented-item-selected{
  color: #000;
  background: #cccccc;
  border-radius: 0;
  box-shadow: none;
}

.undp-segmented-small.data-platform-segmented.ant-segmented .ant-segmented-item-label {
  line-height: 36px;
}
.undp-segmented-small.data-platform-segmented-small .ant-segmented-thumb{
  color: #000;
  background: #cccccc;
  border-radius: 0;
  box-shadow: none;
}
.undp-segmented-small.data-platform-segmented-small.ant-segmented .ant-segmented-item {
  border-radius: 0;
  position: relative;
}
.undp-segmented-small.data-platform-segmented-small.ant-segmented .ant-segmented-item:after {
  content: '';
  position: absolute;
  left: -1px;
  top: 6px;
  bottom: 6px;
  width: 1px;
  background: #cccccc;
  height: auto;
}
.undp-segmented-small.data-platform-segmented-small.ant-segmented .ant-segmented-item:first-child:after {
  content: none;
}
.undp-segmented-small.data-platform-segmented-small.ant-segmented .ant-segmented-item-label {
  line-height: 36px;
}
